import router from '@/router'
import { cloneDeep } from 'lodash'
import { database as getDatabase, dbGet, dbSet } from '@/libs/util.db'

export default {
  namespaced: true,
  actions: {
    /**
     * @description  | 
     * @description  dbName.path = value
     * @param {Object} context
     * @param {Object} payload dbName {String} 
     * @param {Object} payload path {String} 
     * @param {Object} payload value {*} 
     * @param {Object} payload user {Boolean} 
     */
    set (context, {
      dbName = 'database',
      path = '',
      value = '',
      user = false
    }) {
      dbSet({ dbName, path, value, user })
    },
    /**
     * @description 
     * @description  dbName.path || defaultValue
     * @param {Object} context
     * @param {Object} payload dbName {String} 
     * @param {Object} payload path {String} 
     * @param {Object} payload defaultValue {*} 
     * @param {Object} payload user {Boolean} 
     */
    get (context, {
      dbName = 'database',
      path = '',
      defaultValue = '',
      user = false
    }) {
      return dbGet({ dbName, path, defaultValue, user })
    },
    /**
     * @description 
     * @param {Object} context
     * @param {Object} payload user {Boolean} 
     */
    database (context, {
      user = false
    } = {}) {
      return getDatabase({
        user,
        defaultValue: {}
      })
    },
    /**
     * @description 
     * @param {Object} context
     * @param {Object} payload user {Boolean} 
     */
    databaseClear (context, {
      user = false
    } = {}) {
      return getDatabase({
        user,
        validator: () => false,
        defaultValue: {}
      })
    },
    /**
     * @description  [  ]
     * @param {Object} context
     * @param {Object} payload basis {String}  [ name | path | fullPath ]
     * @param {Object} payload user {Boolean} 
     */
    databasePage (context, {
      basis = 'fullPath',
      user = false
    } = {}) {
      return getDatabase({
        path: `$page.${router.app.$route[basis]}`,
        user,
        defaultValue: {}
      })
    },
    /**
     * @description  [  ]
     * @param {Object} context
     * @param {Object} payload basis {String}  [ name | path | fullPath ]
     * @param {Object} payload user {Boolean} 
     */
    databasePageClear (context, {
      basis = 'fullPath',
      user = false
    } = {}) {
      return getDatabase({
        path: `$page.${router.app.$route[basis]}`,
        user,
        validator: () => false,
        defaultValue: {}
      })
    },
    /**
     * @description  ( $data ) 
     * @param {Object} context
     * @param {Object} payload instance {Object} vue 
     * @param {Object} payload basis {String}  [ name | path | fullPath ]
     * @param {Object} payload user {Boolean} 
     */
    pageSet (context, {
      instance,
      basis = 'fullPath',
      user = false
    }) {
      return getDatabase({
        path: `$page.${router.app.$route[basis]}.$data`,
        user,
        validator: () => false,
        defaultValue: cloneDeep(instance.$data)
      })
    },
    /**
     * @description 
     * @param {Object} context
     * @param {Object} payload instance {Object} vue 
     * @param {Object} payload basis {String}  [ name | path | fullPath ]
     * @param {Object} payload user {Boolean} 
     */
    pageGet (context, {
      instance,
      basis = 'fullPath',
      user = false
    }) {
      return dbGet({
        path: `$page.${router.app.$route[basis]}.$data`,
        user,
        defaultValue: cloneDeep(instance.$data)
      })
    },
    /**
     * @description 
     * @param {Object} context
     * @param {Object} payload basis {String}  [ name | path | fullPath ]
     * @param {Object} payload user {Boolean} 
     */
    pageClear (context, {
      basis = 'fullPath',
      user = false
    }) {
      return getDatabase({
        path: `$page.${router.app.$route[basis]}.$data`,
        user,
        validator: () => false,
        defaultValue: {}
      })
    }
  }
}
